
// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import UiLoader from '@components/ui-loader'
import { useForm, Controller } from 'react-hook-form'
import classnames from 'classnames'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
// ** Invoice List Sidebar
import Breadcrumbs from '@components/breadcrumbs'
// ** Columns
// import { columns } from './columns'
// import AddQuiz from '../add'
import Swal from 'sweetalert2'
// ** Store & Actions //deletefixturefootball, viewfootballfixture, 
import { getfootballTotal, clearData, footballfixtoggle, deletefixturefootball, viewfootballfixture, getFootballFixtureadd, searchFootballFixture, getfootballfixturedownload } from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
// import { selectThemeColors } from '@utils'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter, HelpCircle, Edit, Trash2  } from 'react-feather' //Edit Trash2 
import DataTable from 'react-data-table-component'
import Moment from "moment"
//import UiLoader from '@components/ui-loader'
import { Card, Row, Col, Label, Button, Spinner, Badge, CustomInput, Popover, PopoverHeader, PopoverBody, CardHeader } from 'reactstrap'
import { getAllSeries, getAllFixture, collectseriesid, collectfixtureid } from '../../../Report/football/store/actions'
// ** Styles
import withReactContent from 'sweetalert2-react-content'
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import { useHistory } from 'react-router-dom'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import AppCollapse from '@components/app-collapse'

// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"
import { emptyId } from '../../../Report/CricketReport/store/actions'

// ** Table Header
const CustomHeader = () => {


  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">

    </div>
  )
}

const UsersList = () => {

  const statusOptions = [
    { value: '1', label: 'Live' },
    { value: '2', label: 'Upcoming' },
    { value: '3', label: 'Completed' },
    { value: '4', label: 'Cancelled' }
  ]

  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.fix)
  const store1 = useSelector((state) => state.fix)
  const sto = useSelector((state) => state.reports)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [popoverOpen, setPopoverOpen] = useState(false)
  const [listData, setListData] = useState([])
  const [status, setStatus] = useState(false)
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [paginate, setPaginate] = useState(0)
  const MySwal = withReactContent(Swal)
  const history = useHistory()
  const [sid, setSid] = useState(0)
  const [fid, setFid] = useState(0)
  const [val, setVal] = useState(false)
  const [serilist, setSerilist] = useState("")
  const [pickerfrom, setPickerfrom] = useState(false)
  const [pickerto, setPickerto] = useState(false)
  const [fixlist, setFixlist] = useState("")
  const [refresh, setRefresh] = useState(false)
  const [error, setError] = useState(false)
  const [searchStatus, setSearchStatus] = useState('')
  const [searchType] = useState('')
  const [page, setPage] = useState(1)
  const [ccpicker, setCcpicker] = useState("")
  const [ccdateto, setCcdateto] = useState("")
  // const [value] = useState(null) 
  const [dataPerPage, setDataPerPage] = useState('')
  const [series, setSeries] = useState('')
  Moment().format("MMM Do YY")

  useEffect(() => {
    return () => {
      dispatch(emptyId())
      // dispatch(clearData())
    }
  }, [])

  useEffect(() => {
    return () => {
      dispatch(clearData())
    }
  }, [])

  // ** Get data on mount

  useEffect(() => {
    dispatch(getfootballTotal(page))
  }, [page])

  const download = () => {
    dispatch(getfootballfixturedownload(serilist, fixlist, searchStatus, ccpicker === "NaN-NaN-NaN" ? "" : ccpicker, ccdateto === "NaN-NaN-NaN" ? '' : ccdateto))
  }

  useEffect(() => {        
    const idea = `{"series_id":"${sto.football_seriesid ? sto.football_seriesid : ""}", 
    "fixture_id":"${sto.football_fixtureid ? sto.football_fixtureid : ""}",
    "page":${currentPage}, "from":"${ccpicker === "NaN-NaN-NaN" ? '' : ccpicker}",
    "to":"${ccdateto === "NaN-NaN-NaN" ? '' : ccdateto}", "fixture_status":"${searchStatus ? searchStatus : ""}", "limit":"${dataPerPage ? dataPerPage : ""}"}`

    if (store.football_add_data.length !== 0) {
      dispatch(getFootballFixtureadd(idea))
      dispatch(searchFootballFixture(currentPage))
    } else {
      dispatch(searchFootballFixture(currentPage))
    }
    dispatch(getAllSeries())
  }, [paginate, store.football_toggle])

  useEffect(() => {
    setCurrentPage(1)
    dispatch(getFootballFixtureadd(`{"series_id":"${sto.football_seriesid ? sto.football_seriesid : ""}", 
    "fixture_id":"${sto.football_fixtureid ? sto.football_fixtureid : ""}",
    "page":${1}, "from":"${ccpicker === "NaN-NaN-NaN" ? '' : ccpicker}",
    "to":"${ccdateto === "NaN-NaN-NaN" ? '' : ccdateto}", "fixture_status":"${searchStatus ? searchStatus : ""}", "limit":"${dataPerPage ? dataPerPage : ""}"}`))
  }, [dataPerPage])

  useEffect(() => {
    if (store.isloading) {
      <Spinner />
    } else {
      setListData(store.football_data)
    }
  }, [store.isloading])

  const handleConfirmText = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ml-1'
      },
      buttonsStyling: false
    }).then(function (result) {
      if (result.value) {
        dispatch(deletefixturefootball(id))
      }
    })
  }

  const edit = (date) => {
    setVal(false)
    setPickerto(true)
    const local = new Date(date)
    const datestring = `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
    setCcdateto(datestring)
    console.log(datestring)
  }

  const edittime = (date) => {
    setVal(false)
    setPickerfrom(true)
    const local = new Date(date)
    const datestring = `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
    setCcpicker(datestring)
    console.log(datestring)
  }

  const editHandler = (row) => {    
     history.push({pathname:"/football/fixture/edit"})
     dispatch(viewfootballfixture(row.id))    
   }


  const SwitchClick = (row) => {
    setStatus(!status)
    dispatch(footballfixtoggle(row, status))
  }
  console.log(store)

  const statusObj = {
    1: 'light-success',
    2: 'light-warning',
    3: 'light-danger',
    4: 'danger'
  }

  const formatDate = (value, formatting = { month: 'short', day: 'numeric', year: 'numeric', hour: 'numeric', minute: 'numeric', second: 'numeric' }) => {
    if (!value) return value
    return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))
  }

  const columns = [
    {
      name: '#',
      minWidth: '20px',
      cell: (row, pos) => `#${store.football_page?.data?.length ? store.football_page?.from + pos : store.football_page?.data?.from + pos}`
    },
    {
      name: 'Name',
      minWidth: '250px',
      selector: 'fixture_name',
      sortable: true,
      cell: (row) => row.fixture_name

    },
    {
      name: 'Short Name',
      minWidth: '250px',
      selector: 'fixture_sh',
      sortable: true,
      cell: (row) => (
        <Row>
          <Col>
            <img src={row.team_a_logo} style={{ height: '25px', width: '25px' }} />
          </Col>
          <div style={{width:`${100}px`, overflow:'hidden', textOverflow:'ellipsis', textAlign: 'center'}}> {row.fixture_sh}</div>
          <Col>
            <img src={row.team_b_logo} style={{ height: '25px', width: '25px' }} />
          </Col>
        </Row>
      )

    },
    {
      name: 'Date&Time',
      minWidth: '200px',
      selector: 'date_time',
      sortable: true,
      //cell: (row) => Moment((row.date_time), 'DD-MM-YYYY').format("DD MMM YYYY HH:MM:SS")
      cell: (row) => formatDate(row.date_time)
    },
    {
      name: 'Series Name',
      minWidth: '250px',
      selector: 'series_name',
      sortable: true,
      cell: (row) => (
        <div style={{ width: '250px', overflow: 'hidden', textOverflow: 'ellipsis' }}>
          {row.series_name}
        </div>
      )
    },
    {
      name: 'Status',
      minWidth: '160px',
      selector: 'fixture_status',
      sortable: true,
      cell: (row) => (
        <Badge className="text-capitalize" color={statusObj[row.fixture_status]} pill>
          {row.fixture_status === 1 ? "Live" : row.fixture_status === 2 ? 'Upcoming ' : row.fixture_status === 3 ? 'Completed' : row.fixture_status === 4 ? "Cancelled" : ''}
        </Badge>
      )

    },
    {
      name: 'Action',
      minWidth: '138px',
      selector: 'active',
      cell: (row) => (
        <div className="column-action">
          {
            row.fixture_status === 2 ?
              (
                <CustomInput
                  className='custom-control-success'
                  type='switch'
                  id={`success${row.id}`}
                  name='success'
                  onChange={() => SwitchClick(row.id)}
                  inline
                  defaultChecked={row.active !== 0}
                />
              ) : (
                <CustomInput
                  className='custom-control-danger'
                  type='switch'
                  id={`success${row.id}`}
                  name='success'
                  inline
                  defaultChecked
                  disabled
                />
              )
          }
        </div>
      )
    },
    {
      name: 'Active',
      minWidth: '138px',
      selector: 'active',
      sortable: true,
      cell: (row) => (
        <div className="column-action">
          {/* <a href={`/walls/edit/${row.feed_id}`}> */}
          <Edit width="17" className="cursor-pointer" title="Edit" onClick={() => editHandler(row)} />
          {/* </a> */}
          <Trash2 width="17" className="ml-2 cursor-pointer" title="Add Feed" onClick={() => handleConfirmText(row.id)} />
        </div>
      )
     }

  ]

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setPaginate(paginate + 1)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value)
    setRowsPerPage(value)
    setCurrentPage(1)
  }

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {
      const filterList = store.football_data.filter((userItem) => {

        if (userItem.player_type) {
          return userItem.player_type.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.football_data)
    }
  }, [searchTerm])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = store.football_add_data?.data?.length > 0 ? store.football_add_data?.last_page : store.footballsearchFixture?.data?.length > 0 ? store.footballsearchFixture?.last_page : store.football_page.last_page

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }
  console.log(store)
  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (store?.football_add_data?.data?.length >= 0) {
      return store?.football_add_data?.data
    } else if (store.footballsearchFixture?.data?.length) {
      return store.footballsearchFixture?.data
    } else if (listData.length === 0 && isFiltered) {
      return []
    }
  }

  const defaultValues = {
    reactSelect: null
  }
  const { handleSubmit, control } = useForm({ defaultValues })


  const fixture = sto.football_fixture_name?.map(function (fix) {
    return { value: fix.id, label: fix.fixture_name }
  })

  const filterid = sto.football_series_name?.filter(s => s.series_name === serilist)
  const filterid2 = sto.football_fixture_name?.filter(f => f.fixture_name === fixlist)
  const filterall = sto.football_fixture_name?.map(f => f.fixture_name)


  useEffect(() => {
    dispatch(getAllFixture(serilist ? serilist : ''))
    console.log(sid)
  }, [serilist])
  const idea = `{"series_id":"${serilist ? serilist : ""}", "fixture_id":"${fixlist ? fixlist : ""}", 
   "page":${1}, "from":"${ccpicker === "NaN-NaN-NaN" ? '' : ccpicker}", "to":"${ccdateto === "NaN-NaN-NaN" ? '' : ccdateto}", 
   "fixture_status":"${searchStatus ? searchStatus : ""}", "limit":"${dataPerPage ? dataPerPage : ""}"}`

  const submit = (e) => {
    e.preventDefault()
    setVal(true)
    setCurrentPage(1)
    console.log(filterall.map(f => f === fixlist))
    dispatch(collectseriesid(serilist ? serilist : ''))
    dispatch(collectfixtureid(fixlist ? fixlist : ''))

    if (serilist === "") {
      setError(true)
      dispatch(getFootballFixtureadd(idea))
    } else {
      dispatch(getFootballFixtureadd(idea))
    }
  }

  useEffect(() => {
    // console.log("not clicked")
    // setListData(store.cricket_teams)
    if (store?.football_add_data.length !== 0) {
      setListData(store?.football_add_data)
    } else {
      if (store?.football_add_data.length === 0 && val) {
        setListData(store?.football_add_data)
      } else {
        setListData(store?.football_data)
      }
    }
  }, [submit])

  const serieslist = (e) => {
    setSeries(e)
    setVal(false)
    console.log(error)
    setError(false)
    setSerilist(e?.value)
    setRefresh(true)
    // dispatch(getAllFixture(filterid[0]?.id))
    setSid(filterid[0]?.id)
    console.log(filterid[0]?.id)
    // return store.football_series_name.filter(s => s.series_name === list)
  }

  const fixturelist = (e) => {
    const datas = e.map(e => e.value)
    setFixlist(datas)
    setRefresh(false)
    console.log(e.map(e => e.value))
    setFid(filterid2[0]?.id)
    console.log(filterid2[0]?.id)
    console.log(fid)
    // return store.football_series_name.filter(s => s.series_name === list)
  }

  useEffect(() => {
    console.log('Mounted')
    return () => {
      console.log('Unmounted')
      dispatch(clearData())
    }
  }, [])

  const handleKeyPress = (e) => {
    if (e.keyCode === 13) {
      setCurrentPage(1)
      dispatch(getFootballFixtureadd(idea))
      dispatch(collectseriesid(serilist ? serilist : 's'))
      dispatch(collectfixtureid(fixlist ? fixlist : ''))
    }
  }
  console.log(searchStatus)

  useEffect(() => {
    const statusCode = searchStatus === 'Live' ? 1 : searchStatus === 'Upcoming' ? 2 : searchStatus === 'Completed' ? 3 : searchStatus === 'Cancelled' ? 4 : ''
    dispatch(searchFootballFixture(currentPage, searchType, statusCode))
  }, [currentPage])

  const statusHandler = (value) => {
    setSearchStatus(value)
    setCurrentPage(1)
  }

  // ************************************************************************************* //

  const options = []
  for (let i = 0; i < store1.football_total?.data?.total; i++) {
    if (page === store1.football_total?.data?.current_page) {
      options.push({
        value: `${store1.football_total?.data?.data[i]?.id}`,
        label: `${store1.football_total?.data?.data[i]?.series_name}`
      })
    }

  }
  const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
  const optionsPerPage = 10

  const loadOptions = async (search, prevOptions, page) => {
    await sleep(1000)
    if (page === store1.football_total?.data?.current_page) {
      const nextData = store1.football_total?.data?.data?.map((nextData) => { return { value: nextData.id, label: nextData.series_name } })
      const scrollLength = [...prevOptions, ...nextData]
      let filteredOptions
      if (!search) {
        filteredOptions = scrollLength.length > 10 ? scrollLength : options
      } else {
        const searchLower = search.toLowerCase()
        filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
        )
      }

      const hasMore = Math.ceil(store1.football_total?.data?.total / optionsPerPage) > page
      const slicedOptions = filteredOptions.slice(
        (page - 1) * optionsPerPage,
        page * optionsPerPage
      )

      return {
        options: slicedOptions,
        hasMore
      }
    }
  }
  const defaultAdditional = {
    page: 1
  }

  const loadPageOptions = async (q, prevOptions, { page }) => {
    setPage(page)
    const { options, hasMore } = await loadOptions(q, prevOptions, page)
    return {
      options: [...options],
      hasMore,

      additional: {
        page: page + 1
      }
    }
  }

  // ************************************************************************************* //

  const data = [
    {
      title: <div><Filter size={18} /> Filter</div>,
      content: (
        <>
          <Row>
            <Col >
              <Label className="mb-0">From:</Label>
              <Controller
                control={control}
                render={({ field: { ...field } }) => (
                  <Flatpickr
                    {...field}
                    value={store.cric_from}
                    onChange={(date) => edittime(date)}
                    options={{
                      dateFormat: "Y-m-d"
                    }}
                    id='range-picker'
                    placeholder='select from date'
                    className={classnames('form-control', { 'is-invalid': ((!pickerfrom && pickerto) && val) || ((pickerfrom && !pickerto) && val) })}
                  />
                )}
                name='reactSelect'
              />
              {((!pickerfrom && pickerto) && val) || ((pickerfrom && !pickerto) && val) ? <h5 className='text-danger ml-3'>Please select a date</h5> : ""}
            </Col>
            &emsp;&nbsp;
            <Col>
              <Label className="mb-0">To:</Label>&nbsp;&nbsp;
              <Controller
                control={control}
                render={({ field: { ...field } }) => (
                  <Flatpickr
                    {...field}
                    value={store.cric_to}
                    onChange={dat => edit(dat)}
                    isClearable={false}
                    options={{
                      dateFormat: "Y-m-d"
                    }}
                    id='range-picker'
                    placeholder='select to date'
                    className={classnames('form-control', { 'is-invalid': ((!pickerfrom && pickerto) && val) || ((pickerfrom && !pickerto) && val) })}
                  />
                )}
                name='reactSelect'
              />
              &emsp;&nbsp;
            </Col>
          </Row>
          <Row>
            <Col className='mb-1' md='6' sm='12'>
              <Label>Series</Label>
              <AsyncPaginate
                menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}
                // className={classnames('react-select', { 'is-invalid': serilist === "" && val })}
                classNamePrefix='select'
                additional={defaultAdditional}
                value={series}
                loadOptions={loadPageOptions}
                onChange={(e) => serieslist(e)}
                onKeyDown={(e) => handleKeyPress(e)}
                isClearable={true}
              />
            </Col>
            <Col className='mb-1' md='6' sm='12'>
              <Label>Fixture</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                isDisabled={sto.fixloader || !serilist}
                onChange={(e) => fixturelist(e)}
                theme={selectThemeColors}
                value={refresh ? "" : fid}
                className='react-select'
                classNamePrefix='select'
                isMulti
                // defaultValue={fixture[0]}
                options={fixture ? fixture : ""}
                placeholder='Select football fixture'
                onKeyDown={(e) => handleKeyPress(e)}
                isClearable={true}
              />
            </Col>
          </Row>
          <Row>
            <Col className='mb-1 mt-1' md='6' sm='12'>
              <Label>Challenges Status:</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                onChange={(e) => statusHandler(e?.value)}
                theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                options={statusOptions}
                placeholder='search challenge status...'
                onKeyDown={(e) => handleKeyPress(e)}
                isClearable={true}
              />
            </Col>
          </Row>
          <Button.Ripple
            color='primary'
            onClick={(e) => handleSubmit(submit(e))}
          >Submit</Button.Ripple>
        </>
      )
    }
  ]

  return (
    <Fragment>
      <Breadcrumbs breadCrumbTitle='Football Fixture' breadCrumbActive='List' />
      <AppCollapse data={data} type='border' accordion className='mb-1' />
      <Card>
        <CardHeader>
          <Col xl="6" className="d-flex align-items-center p-0">
            <div className="d-flex align-items-center w-100">
              <Label for="rows-per-page">Show</Label>
              <CustomInput
                className="form-control"
                type="select"
                id="rows-per-page"
                value={dataPerPage}
                onChange={(e) => setDataPerPage(e.target.value)}
                style={{
                  width: '5rem',
                  padding: '0 0.8rem',
                  backgroundPosition:
                    'calc(100% - 3px) 11px, calc(100% - 20px) 13px, 100% 0'
                }}
              >
                <option value="10">10</option>
                <option value="50">50</option>
                <option value="100">100</option>
              </CustomInput>
              <Label for="rows-per-page">Entries</Label>
            </div>
          </Col>
          <Col className='ml-auto' xl={{ offSet: 5 }}>
            <Button.Ripple
              color='primary'
              disabled={store.date_range > 90}
              onClick={() => download()}>
              {store.download_timer ?
                <>
                  <Spinner color='white' size='sm' />
                  <span className='ml-50'>Loading...</span>
                </> : 'Download CSV'}
            </Button.Ripple>
            <Button.Ripple
              outline id='controlledPopover'
              color='flat-info'
            >
              <HelpCircle />
            </Button.Ripple>
            <Popover
              placement='top'
              target='controlledPopover'
              isOpen={popoverOpen}
              toggle={() => setPopoverOpen(!popoverOpen)}
            >
              <PopoverHeader>INFO</PopoverHeader>
              <PopoverBody>
                pick a date less than 90 days to download
              </PopoverBody>
            </Popover>
          </Col>
        </CardHeader>
        <DataTable
          noHeader
          // pagination
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage={handlePerPage}
              rowsPerPage={rowsPerPage}
              searchTerm={searchTerm}
              currentPage={currentPage}
              handleFilter={handleFilter}
              setListData={setListData}
              football_data={store.football_data}
              football_add_data={store.football_add_data}
              setCurrentPage={setCurrentPage}
            />
          }
        />
      </Card>
      {
        (store?.football_add_data?.data?.length === 0 || store?.football_add_data?.data?.length === undefined || store.footballsearchFixture?.data?.length) ? '' :  <CustomPagination/>
      } 
    </Fragment>
  )
}

export default UsersList